@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');
$dark-blue: hsl(238,
29%,
16%);
$soft-red: hsl(14,
88%,
65%);
$soft-violet: hsl(273,
75%,
66%);
$soft-blue: hsl(240,
73%,
65%);
$dark-gray-blue: hsl(237,
12%,
33%);
$gray-blue: hsl(240,
6%,
50%);
$light-gray-blue: hsl(240,
5%,
91%);
body {
    font-family: "Kumbh Sans", sans-serif;
    background: $soft-blue;
    background: linear-gradient(0deg, $soft-blue 0%, $soft-violet 50%);
    #page-faq {
        height: 100vh;
        .row {
            border-radius: 1.5rem;
        }
    }
    h1 {
        font-weight: 700;
        font-size: 2.5rem;
    }
    .faq-list-item {
        cursor: pointer;
        padding: 1rem 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.15);
        font-size: 1.1rem;
        .arrow-icon {
            width: .8rem;
            float: right;
            margin-right: 1.5rem;
            margin-top: .75rem;
        }
        .content {
            display: none;
            font-size: 1rem;
        }
    }
    .faq-list-item.active {
        .content {
            display: block;
        }
    }
    #illustration-container {}
}